<template>
  <div class="verification-wrapper">
    <el-button type="primary" @click="valid">验证</el-button>
  </div>
</template>

<script>
import Verfication from "@/components/verfication/index.js";
export default {
  components: {},
  mounted() {},
  methods: {
    login(id) {
      this.axios.get("/api/login?id=" + id).then((res) => {});
    },
    valid() {
      Verfication.alert({
        requestUrl: "/api/generate",
        validUrl: "/api/verify",
        type: "SLIDER",
      }).then((res) => {
        this.login(res.id);
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.verification-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
</style>