<template>
  <div
    class="lt-verification__wrapper"
    v-if="visible"
    role="dialog"
    @click="closeBg"
  >
    <div id="captcha-box" class="lt-captcha-box"></div>
  </div>
</template>

<script>
import Load from "../js/load";
export default {
  data() {
    return {
      visible: false,
      action: "",
      type: "",
      requestUrl: "",
      validUrl: "",
    };
  },
  mounted() {},
  watch: {
    visible(val) {
      if (val) {
        this.uid++;
        this.$nextTick(() => {
          this.showValid();
        });
      }
    },
  },
  methods: {
    showValid() {
      /** 滑块：SLIDER / 旋转：ROTATE / 拼接：CONCAT /文字图片点选：WORD_IMAGE_CLICK*/
      if (
        ["SLIDER", "ROTATE", "CONCAT", "WORD_IMAGE_CLICK"].includes(this.type)
      ) {
        this.requestUrl = `${this.requestUrl}?type=${this.type}`;
      }

      if (!this.requestUrl) {
        this.doClose();
        throw new Error("必须配置 [requestUrl]请求验证码接口");
      }

      if (!this.validUrl) {
        this.doClose();
        throw new Error("必须配置 [validUrl]验证验证码接口");
      }

      this.initValid();
    },
    initValid() {
      // config 对象为TAC验证码的一些配置和验证的回调
      const config = {
        // 生成接口 (必选项,必须配置, 要符合tianai-captcha默认验证码生成接口规范)
        requestCaptchaDataUrl: this.requestUrl,
        // 验证接口 (必选项,必须配置, 要符合tianai-captcha默认验证码校验接口规范)
        validCaptchaUrl: this.validUrl,
        // 验证码绑定的div块 (必选项,必须配置)
        bindEl: "#captcha-box",
        // 验证成功回调函数(必选项,必须配置)
        validSuccess: (res, c, tac) => {
          this.handleAction("confirm", res.data);
          // 调用具体的login方法
        },
        // 验证失败的回调函数
        validFail: (res, c, tac) => {
          // 验证失败后重新拉取验证码
          tac.reloadCaptcha();
        },
        // 刷新按钮回调事件
        btnRefreshFun: this.throttle((el, tac) => {
          tac.reloadCaptcha();
        }, 1000),
        // 关闭按钮回调事件
        btnCloseFun: (el, tac) => {
          try {
            // tac.destroyWindow();
            this.handleAction("close");
          } catch (error) {
            console.log(error);
          }
        },
      };
      // 一些样式配置， 可不传
      let style = {
        logoUrl: null, // 去除logo
        moveTrackMaskBgColor: "#00479d",
        moveTrackMaskBorderColor: "#00479d",
      };
      Load.initTAC(config, style);
    },

    handleAction(action, param) {
      this.action = action;
      this.doClose(param);
    },
    doClose(param = null) {
      if (!this.visible) return;
      this.visible = false;
      this._closing = true;
      setTimeout(() => {
        if (this.action) {
          this.callback(this.action, param);
        }
      });
    },
    closeBg() {
      if (document.querySelector("#tianai-captcha-parent")) {
        return;
      }
      try {
        this.showValid();
      } catch (error) {
        this.action = "";
        this.doClose();
      }
    },
    //节流
    throttle(func, delay) {
      let lastCall = 0;
      return function (...args) {
        const now = new Date().getTime();
        if (now - lastCall >= delay) {
          lastCall = now;
          func.apply(this, args);
        }
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.lt-verification__wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
}
.lt-verification__wrapper::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 0;
  vertical-align: middle;
}
.lt-captcha-box {
  display: inline-block;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 4px;
  // border: 1px solid #ebeef5;
  font-size: 18px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  text-align: left;
  overflow: hidden;
  backface-visibility: hidden;
}
</style>