import "../css/tac.css";
import "../js/tac.min.js";
import Vue from 'vue';
import verifyVue from './main.vue';
import merge from '@/utils/merge';

const defaults = {
  type: '',
  requestUrl: '',
  validUrl: ''
};
const VerifyConstructor = Vue.extend(verifyVue);

let currentVerify, instance;
let verifyQueue = [];

const defaultCallback = (action = '', param) => {
  if (currentVerify) {
    let callback = currentVerify.callback;
    if (typeof callback === 'function') {
      callback(action);
    }
    if (currentVerify.resolve) {
      if (action === 'confirm') {
        currentVerify.resolve(param);
      }
    }
  }
};

// 初始化实例
const initInstance = () => {
  instance = new VerifyConstructor({
    el: document.createElement('div')
  });

  instance.callback = defaultCallback;
};

//显示下一个校验
const showNextVerify = () => {
  if (!instance) {
    initInstance();
  }
  if (!instance.visible) {
    if (verifyQueue.length > 0) {
      currentVerify = verifyQueue.shift();

      //给实例赋予当前的配置项
      let options = currentVerify.options;
      for (let prop in options) {
        if (options.hasOwnProperty(prop)) {
          instance[prop] = options[prop];
        }
      }
      if (options.callback === undefined) {
        instance.callback = defaultCallback;
      }

      let oldCb = instance.callback;
      instance.callback = (action, instance) => {
        oldCb(action, instance);
        showNextVerify();
      };
      document.body.appendChild(instance.$el);

      Vue.nextTick(() => {
        instance.visible = true;
      });
    }
  }
};

const Verfication = function (options) {
  if (Vue.prototype.$isServer) return;
  //将配置和回调添加到验证队列
  if (typeof Promise !== 'undefined') {
    return new Promise((resolve, reject) => {
      verifyQueue.push({
        options: merge({}, defaults, Verfication.defaults, options),
        callback: options.callback,
        resolve: resolve,
        reject: reject
      });
      showNextVerify();
    })
  } else {
    verifyQueue.push({
      options: merge({}, defaults, Verfication.defaults, options),
      callback: callback
    });

    showNextVerify();
  }
};

Verfication.setDefaults = defaults => {
  Verfication.defaults = defaults;
};

//弹出验证窗口
Verfication.alert = (options) => {
  return Verfication(merge({}, options));
};

//关闭验证窗口
Verfication.closeAll = () => {
  instance.doClose();
  instance.visible = false;
  verifyQueue = [];
  currentVerify = null;
};

export default Verfication;
export { Verfication };
