const Math = window.Math
    , head = document.getElementsByTagName("head")[0]
    , TAC_LOADING_DIV = '<div id="tac-loading" style="border: 1px solid #eee;/* background-color: #409EFF; */border-radius: 5px;width: 318px;height: 318px;line-height: 318px;color: #606266;text-align: center;position: relative;box-sizing: border-box;">请稍等...</div>';
function showLoading(e) {
    var t = document.querySelector(e);
    if (!t) {
        setTimeout(() => {
            showLoading(e)
        }, 200);
    }
    t && (t.innerHTML = TAC_LOADING_DIV)
}
function hideLoading(e) {
    let t = document.querySelector(e);
    t && (t.innerHTML = "")
}
function initTAC(t, n) {
    function getCode() {
        hideLoading(t.bindEl);
        let tac = new TAC(t, n)
        tac.init()
    }
    showLoading(t.bindEl)
    getCode()
}

export default {
    initTAC
}